.Pic {
width: 200px;
height: 280px;
background-color: white;
border-radius: 20px;
box-shadow: 3px 10px 5px rgb(2, 82, 9);
margin: 45px;
}

.uppercont #c1{
    height: 60px;
    background: linear-gradient(to left,rgb(7, 87, 5),rgb(172, 228, 181));
    border-radius: 20px;
}
.uppercont #c2{
    height: 60px;
    background: linear-gradient(to left,rgb(49, 2, 80),rgb(247, 168, 236));
    border-radius: 20px;
}
.uppercont #c3{
    height: 60px;
    background: linear-gradient(to left,rgb(2, 14, 66),rgb(12, 233, 185));
    border-radius: 20px;
}

.image-cont img{
background:white;
width: 130px;
height: 200px;
transform: translate(38px,10px);
border-radius:20px ;
}
.lower-cont{
    margin-top: 150px;
    height: 300px;
    text-align: center;
    color: rgb(2, 82, 51);
   
}
.lower-cont :hover{
    background-color: rgb(172, 238, 151);
    border: 10px;
    border-radius: 10px;
    height: 60px;
}
