.Card{
    width: 300px;
    height: 400px;
    background: white;
    border-radius: 10px;
    box-shadow: 0px 1px 10px 1px black;
    margin: 25px;
}


.upper-container{
    height: 100px;
    background: linear-gradient(to left ,#2d00f7,#ff0291);

}

.image-container img{
    background: white;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    padding: 5px;
    transform: translate(95px,45px);
}

img{
    border:2px solid #8e00b9;
}

.lower-container{
    margin-top: 60px;
    background: white;
    text-align: center;
    font-family: sans-serif;
}

.lower-container h3,h4,p{
    margin: 0;
    padding: 0;
}

.lower-container h3{
    color: #2f2d2e;
    font-weight: 600;
    font-size: 1.5rem;
    font-family: 'Acme', sans-serif;
}

.lower-container h4{
    color: #8e00b9;
    font-weight: 700;
    font-size: 1.2rem;
}

.lower-container p{
    margin: 1rem;
    font-weight: 300;
    color: #2f2d2e;
    font-size: 0.9rem;
    text-align: justify;
    /* font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
    font-size: 0.9rem;
    font-family: 'Acme', sans-serif;
} 